.featured {
  width: 100%;
  max-width: 1080px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  z-index: 1;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.featuredItem {
  position: relative;
  color: white;
  border-radius: 10px;
  overflow: hidden;
  height: 250px;
  flex: 1;

  div {
    position: absolute;
    bottom: 20px;
    left: 20px;
  }
}
